.prev-next__link {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
  transition: all 1200ms 400ms cubic-bezier(0.23, 1, 0.32, 1);
  transform: translate3d(0, 0, 0); }
  @media all and (min-width: 1200px) {
    .prev-next__link {
      padding: 8px; } }
  .js-is-loading .prev-next__link {
    opacity: 0;
    transform: translate3d(-100%, 0, 0); }

.prev-next__link--right {
  left: auto;
  right: 0; }
  .js-is-loading .prev-next__link--right {
    opacity: 0;
    transform: translate3d(100%, 0, 0); }

