/* =========================================
Placeholder
========================================= */

/**
 * Convenience mixin to
 * help style input
 * placeholder
 * text
 *
 */
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }
  &:-moz-placeholder { /* Firefox 18- */
    @content;
    opacity: 1;
  }
  &::-moz-placeholder {  /* Firefox 19+ */
    @content;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    @content;
  }
}
